
.wrapper-contact {
    background: rgb(0,255,149);
    background: linear-gradient(90deg, rgba(0,255,149,1) 0%, rgba(16,199,237,1) 86%);
    padding-top: 7rem;
    height: 100vh;
}
.form-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    width: 40%;
    
    -webkit-box-shadow: 0px 10px 13px -7px #000000, 7px 6px 11px -3px rgba(0,0,0,0); 
    box-shadow: 0px 10px 13px -7px #000000, 7px 6px 11px -3px rgba(0,0,0,0);
}
@media(max-width:920px) {
    .form-wrapper{
        width: 80%;
        margin: 0 auto;
    }
}

.form-inner {
    padding: 2% 0;
}
#titlu-contact {
    margin-top: 3rem;
}
.input {
    width: 100%;
    height: 40px;
    border: none;
    border-bottom: 2px solid rgb(79, 129, 129);
}
.label {
    font-size: 1.3rem;
}
.textarea{
    height: 100px;
    border: none;
    border-bottom: 2px solid rgb(79, 129, 129);
}