.wrapper-cv{
    margin:auto;
    position: relative;
}

.partea1 main{
    min-height: 100vh;
}
.descriere{
    position: absolute;
    padding: 4%;
    top: 10%;
    left: 25%;
    width: 50%;
    background-color: rgb(252, 252, 252);
    border-radius: 7px;
    box-shadow: 1px 0px 11px 4px rgba(0,0,0,0.75);
    -webkit-box-shadow: 0px 10px 13px -7px #000000, 7px 6px 11px -3px rgba(0,0,0,0); 
    box-shadow: 0px 10px 13px -7px #000000, 7px 6px 11px -3px rgba(0,0,0,0);
}
@media (max-width: 600px) {
    .descriere{
        left: 5%;
        width: 90%;
    }
}
.descriere h1{
    text-align: center;
    font-size: 4rem;
}
.poza{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.container-poza{
    height: 150px;
    width: 150px;
    margin-right: 2rem;
}
.container-poza img{
    border-radius: 50%;
}

.contor-skill{
    display: flex;
    border-radius: 10%;
}
.contor-true{
    height: 5px;
    background-color: rgb(199, 130, 2);
    border-radius: 40px;
}
.contor-false{
    height: 5px;
    background-color: rgb(114, 110, 110);
    border-radius: 40px;
}

.contor-1{
    width: 25%;
}
.contor-2{
    width: 25%;
}
.contor-3{
    width: 40%;
}
.contor-4{
    width: 10%;
}
.contor-5{
    width: 35%;
}
.contor-6{
    width: 15%;
}
.contor-7{
    width: 30%;
}
.contor-8{
    width: 20%;
}
.contor-9{
    width: 25%;
}
.contor-10{
    width: 25%;
}

.partea2{
    min-height: 100vh;
    background-image: url('https://monodomo.com/free-wallpapers/dark-nature-wallpapers-phone-For-Free-Wallpaper.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    background-attachment: fixed;
}