* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


#header {
    background-image: url("https://wallpaperaccess.com/full/99791.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position: top;
    height: 80vh;
    color: #ffb037;
    text-align: center;
    width: 88%;
    margin: 5rem auto;
}
.wrapper-home{
    width: 100%;
    display: flex;
    flex-direction: column;
}

#main-home{
    background-image: url("https://wallpaperaccess.com/full/99791.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position: top;
    text-align: center;
    color: white;
    font-size: larger;
    width: 88%;
    margin: 5rem auto;
}

.titlu-main{
    padding: 4%;
    text-align: center;
}
.wrapper-main-home{
    margin: 7rem 0;
}
.wrapper-main-home h2{
    font-size: 3.4rem;
    margin-bottom: 3rem;
    color: #ffb037;
}
.wrapper-main-home h4{
    font-size: 2rem;
}
.wrapper-main-home p{
    color: #ffe268;
}

.wrapper-main-home i{
    margin: 5%;
    font-size: 800%;
    color: #5eaaa8;
}
.chestii{
    padding: 5% 2%;
}